"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { motion, AnimatePresence } from "framer-motion"
import { Menu, X, ShoppingCart, User, Phone, LogIn } from "lucide-react"
import { cn } from "@/lib/utils"

export default function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false)
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const pathname = usePathname()

  // Handle scroll effect
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  // Close mobile menu on route change
  useEffect(() => {
    setMobileMenuOpen(false)
  }, [pathname])

  const navLinks = [
    { name: "Home", href: "/" },
    { name: "Our Products", href: "/products" },
    { name: "About Us", href: "/about" },
    { name: "Contact", href: "/contact" },
  ]

  return (
    <>
      <nav
        className={cn(
          "fixed top-4 left-1/2 -translate-x-1/2 z-50 transition-all duration-300 w-[95%] max-w-5xl",
          "rounded-full border border-white/10 shadow-2xl backdrop-blur-md",
          isScrolled
            ? "bg-primary/95 py-2 top-2"
            : "bg-primary/90 py-3"
        )}
      >
        <div className="px-6 md:px-8 flex items-center justify-between h-14">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-2 group">
            <div className="w-8 h-8 bg-white/10 rounded-full flex items-center justify-center text-white font-bold shadow-sm group-hover:bg-white/20 transition-colors">
              M
            </div>
            <span className="font-heading font-bold text-lg tracking-tight text-white">
              Maa Ilay
            </span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-8">
            {navLinks.map((link) => (
              <Link
                key={link.name}
                href={link.href}
                className="text-sm font-medium text-white/80 hover:text-white transition-colors"
              >
                {link.name}
              </Link>
            ))}
          </div>

          {/* Desktop Actions */}
          <div className="hidden md:flex items-center gap-4">
            <Link
              href="/login"
              className="px-5 py-2 rounded-full bg-secondary text-white font-semibold text-xs hover:bg-secondary-dark transition-all shadow-lg hover:shadow-xl hover:scale-105 active:scale-95"
            >
              Contact
            </Link>
          </div>

          {/* Mobile Toggle */}
          <button
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="md:hidden text-white p-1 hover:bg-white/10 rounded-full transition-colors"
            aria-label="Toggle menu"
          >
            {mobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
          </button>
        </div>
      </nav>

      {/* Mobile Menu Overlay */}
      <AnimatePresence>
        {mobileMenuOpen && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.2 }}
            className="fixed inset-x-4 top-24 bottom-4 z-40 bg-white/95 backdrop-blur-xl rounded-3xl border border-white/20 flex flex-col p-6 shadow-2xl md:hidden overflow-hidden"
          >
            <div className="flex flex-col gap-6 mt-4">
              {navLinks.map((link) => (
                <Link
                  key={link.name}
                  href={link.href}
                  className="text-xl font-medium text-foreground hover:text-primary border-b border-border/40 pb-4"
                >
                  {link.name}
                </Link>
              ))}

              <div className="mt-8 flex flex-col gap-4">
                <Link
                  href="/login"
                  className="flex items-center justify-center gap-2 w-full py-4 rounded-xl bg-primary text-white font-semibold text-lg shadow-md"
                >
                  <LogIn className="w-5 h-5" />
                  Login / Register
                </Link>
                <a
                  href="tel:+919876543210"
                  className="flex items-center justify-center gap-2 w-full py-4 rounded-xl bg-secondary/10 text-secondary-dark font-semibold text-lg border-2 border-secondary/20"
                >
                  <Phone className="w-5 h-5" />
                  Call Support
                </a>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  )
}
